import React from 'react';
import { Link, Route, Switch, useLocation } from 'react-router-dom';
import { useTransition, animated } from 'react-spring';
import SiteHeader from './components/SiteHeader';
import SiteFooter from './components/SiteFooter';
import Page404 from './pages/404';
import PageMain from './pages/Main';
import PageGeneralInterests from './pages/General/Interests';
import PageGeneralLearn from './pages/General/Learn';
import PageWebFuture from './pages/Web/Future';
import PageWebInterests from './pages/Web/Interests';
import PageWebProjects from './pages/Web/Projects';
import './App.css';

const App = () => {
	const location = useLocation();
	let distanceFromTop = 0;
	let distanceFromLeft = 0;
	const transitions = useTransition(location, location => location.pathname, {
		initial: {
			position: 'relative',
		},
		from: {
			opacity: 0,
			top: -50,
		},
		enter: {
			position: 'relative',
			opacity: 1,
			top: 0,
			scrollPercent: 1.0,
		},
		leave: {
			position: 'absolute',
			opacity: 0,
			top: -50,
			scrollPercent: 0.0,
		},
		onStart: (item, key) => {
			// If we're about to transition out of a page
			if (key === 'leave') {
				// Get a snapshot of scrolled distances from the top-left of the page to calculate auto-scroll with
				distanceFromTop  = window.pageYOffset || document.documentElement.scrollTop;
				distanceFromLeft = window.pageXOffset || document.documentElement.scrollLeft;
			}
		},
		onFrame: (item, key, props) => {
			// If we're transitioning out of a page
			if (key === 'leave') {
				// If there's any distance to travel
				if (distanceFromLeft || distanceFromTop) {
					// Scroll to the next percent-calculated position
					window.scrollTo(props.scrollPercent * distanceFromLeft, props.scrollPercent * distanceFromTop);

					// If we have no more space above to scroll, disable vertical scroll
					const top  = window.pageYOffset || document.documentElement.scrollTop;
					if (top === 0) {
						distanceFromTop = 0;
					}
					// If we have no more space leftward to scroll, disable horizontal scroll
					const left = window.pageXOffset || document.documentElement.scrollLeft;
					if (left === 0) {
						distanceFromLeft = 0;
					}
				}
			}
		},
	});

	const ReturnToMain = props => {
		if (location.pathname !== '/') {
			return (
				<nav>
					<ul id="nav-menu">
						<animated.li style={ props }>
							<Link to='/'>I'd like to ask another question…</Link>
						</animated.li>
					</ul>
				</nav>
			);
		}

		return null;
	};

	return (
		<div id="site-wrapper">
			<SiteHeader />
			<div id="site-lower-wrapper">
				{
					transitions.map(({ item: location, props, key }) => (
						<animated.main id="site-main" key={ key } style={ props } >
							<Switch location={ location }>
								<Route exact path="/" component={ PageMain } />
								<Route exact path="/general/interests" component={ PageGeneralInterests } />
								<Route exact path="/web/interests" component={ PageWebInterests } />
								<Route exact path="/web/projects" component={ PageWebProjects } />
								<Route exact path="/web/future" component={ PageWebFuture } />
								<Route exact path="/general/learn" component={ PageGeneralLearn } />
								<Route component={ Page404 } />
							</Switch>
							<ReturnToMain />
						</animated.main>
					))
				}
				<SiteFooter />
			</div>
		</div>
	);
};

export default App;
