const generateNewLinkData = () => {
	const newData = [];
	let dataCounter = 0;
	return {
		data: newData,
		push: (url, text) => {
			newData.push({
				key: dataCounter++,
				url,
				text,
			})
		},
	};
};

const defaultTrail = {
	opacity: 1,
	marginTop: 0,
	from: {
		opacity: 0,
		marginTop: 50,
	},
};

export { generateNewLinkData as default, defaultTrail }; 