import React from 'react';
import './style.css';

import scatter from '../../../assets/images/projects/web/scatter.png';

let projectCards = 0;
const projects = [];

projects.push(
	newProjectCard(
		scatter,
		'ScATTeR',
		'Proof of Concept, React, Sequelize, bCrypt, Express Sessions, JavaScript, HTML 5, CSS 3, Bootstrap, OpenLayers, OpenStreetMap, Geolocation API',
		[
			'This web app is a secure real-time group locator. When you want it.',
			'Quick signups, quick invites, no app downloads, no hassle. Know where everyone is simply by using this website on a mobile browser which supports geolocation.'
		],
		[ 'https://scatter-web.herokuapp.com/' ],
		[
			{
				label: 'Front-End Source Code',
				url: 'https://github.com/j-sea/skatter'
			},
			{
				label: 'Back-End Source Code',
				url: 'https://github.com/j-sea/bailfire'
			},
		]
	),
	newProjectCard(
		undefined,
		'ONE COMMUNITY',
		'Proof of Concept, Express Handlebars, Sequelize, bCrypt, Express Sessions, jQuery, JavaScript, HTML 5, CSS 3, Materialize CSS, Moment.js',
		[
			'This website is a volunteer-based social network designed to bring communities closer.',
			'Have a skill you want to offer for free? Need help with something? Have a volunteer event coming up? Post about it!'
		],
		[ 'https://one-community.herokuapp.com/' ],
		[ 'https://github.com/nicolerobertsb/ONECOMMUNITY' ]
	),
	newProjectCard(
		undefined,
		'Send Care MEOW',
		'Proof of Concept, Single-Page Application, jQuery, JavaScript, HTML 5, CSS 3, Bootstrap, Postmates API, Spotify Web Player, Giphy API, Adobe XD',
		[
			'This web app allows you to send anybody a care package with an optional Postmates order.',
			'Friend or co-worker out sick? Want to help? Send them a mood-lifting digital page with a paid Postmates order ready to be accepted at the touch of a button (after they input their address). If they don\'t accept the order, no transaction or payment occurs.'
		],
		[ 'https://jandystephens.github.io/Send-Care-MEOW' ],
		[ 'https://github.com/jandystephens/Send-Care-MEOW' ]
	),
	newProjectCard(
		undefined,
		'The Silver Lining Game – Launch Website',
		'jQuery, JavaScript, HTML 4, CSS 2, PHP, Adobe Photoshop',
		[
			'This website was built to celebrate and launch the long-awaited fan-game based on the popular Sierra series King\'s Quest.',
			'This game was one of the first fan games to ever receive an official license from the IP holder.',
			'I worked closely with the website\'s graphic designer, Eriq Chang, to ensure the quality of the website.'
		],
		[ 'https://web.archive.org/web/20101128044018id_/http://postudios.com/' ],
		undefined
	),
	newProjectCard(
		undefined,
		'Phoenix Online Studios – Website Redesign',
		'jQuery, JavaScript, HTML 4, CSS 2, PHP, Adobe Photoshop',
		[
			'This website was built to launch at the same time as The Silver Lining\'s launch website.',
			'With their flagship project finally launching, POStudios was looking to redesign their website; however, their web programmer needed to quit leading up to its launch, leaving the website in a state of flux.',
			'I volunteered to take over the work and coordinated with the website\'s graphic designer, Michael Fortunato, to ensure the quality of the website.',
			'I also went above their expectations theming their WordPress blog and Simple Machines forum to match their company redesign.'
		],
		[ 'https://web.archive.org/web/20100703044032id_/http://www.postudios.com/company/index.php' ],
		undefined
	),
	newProjectCard(
		undefined,
		'Wright Medical Technology',
		'jQuery, JavaScript, XHTML, CSS 2, ASP, HTML 5 Video, Adobe Photoshop, GIMP, Handbrake, Batch files',
		[
			'This was a large client of the web firm I worked at, dealing with the advertisement of several original medical products towards physicians and patients.',
			'I was tasked with refactoring their whole suite of websites totalling over a couple hundred pages and bringing all of their code up to W3C standards and prepping everything for internationalization.',
		],
		[ 'https://web.archive.org/web/20111016212306id_/http://www.wmt.com/patients/' ],
		undefined
	)
);

function newProjectCard (previewImage, title, subtitle, bodyParagraphs, demoLinks, sourceLinks) {
	console.log('Creating new project card');

	// Keep track of how many project cards exist, make a local scope variable to persist with closures
	const cardIndex = ++projectCards;

	// If we've got body paragraphs, let's store anything beyond the first paragraph for revealing later
	let initialParagraph;
	let hiddenParagraphs;
	if (bodyParagraphs) {
		if (bodyParagraphs.length !== 0) {
			initialParagraph = <p className="card-text">{ bodyParagraphs[0] }</p>;

			if (bodyParagraphs.length !== 1) {
				hiddenParagraphs = [
					<p key={ `initialParagraph${ cardIndex }` } id={ `initialParagraph${ cardIndex }` } className="text-center"><a href={ `#hiddenParagraphs${ cardIndex }` } className="d-block" role="button" data-toggle="collapse" aria-expanded="false" aria-controls={ `hiddenParagraphs${ cardIndex }` } onClick={ () => document.getElementById('initialParagraph' + cardIndex).setAttribute('class', 'd-none') }>Read more…</a></p>,
					React.createElement(
						'div',
						{
							id: `hiddenParagraphs${ cardIndex }`,
							key: `hiddenParagraphs${ cardIndex }`,
							className: 'collapse'
						},
						bodyParagraphs.reduce(
							(accumulator, paragraph, index) => {
								if (index !== 0) {
									accumulator.push(<p className="card-text" key={ `hiddenParagraphs${ cardIndex }_${ index }` }>{ paragraph }</p>);
								}
								return accumulator;
							}, []
						)
					)
				];
			}
		}
	}

	return (
		<div className="card bg-dark" key={ `card${ cardIndex }` }>
			{
				(previewImage)
				? <img className="card-img-top" src={ previewImage } alt={ "Logo for " + title } />
				: null
			}
			<div className="card-body">
				{
					(title)
					? <h5 className="card-title">{ title }</h5>
					: null
				}
				{
					(subtitle)
					? <h6 className="card-subtitle mb-2">{ subtitle }</h6>
					: null
				}
				{
					(initialParagraph)
					? <>
						{ initialParagraph }
						{ hiddenParagraphs }
					</>
					: null
				}
				{
					(demoLinks || sourceLinks)
					? <div className="card-links text-right">
						{
							(demoLinks)
							? demoLinks.map((link, index) => (
								(typeof link === 'string')
								? <a href={ link } target="_blank" rel="noopener noreferrer" className="btn btn-primary" key={ `link${ index }` }>Demo</a>
								: <a href={ link.url } target="_blank" rel="noopener noreferrer" className="btn btn-primary" key={ `link${ index }` }>{ link.label }</a>
							))
							: null
						}
						{
							(sourceLinks)
							? sourceLinks.map((link, index) => (
								(typeof link === 'string')
								? <a href={ link } target="_blank" rel="noopener noreferrer" className="btn btn-outline-light" key={ `link${ index }` }>Source Code</a>
								: <a href={ link.url } target="_blank" rel="noopener noreferrer" className="btn btn-outline-light" key={ `link${ index }` }>{ link.label }</a>
							))
							: null
						}
					</div>
					: null
				}
			</div>
		</div>
	);
}

export default () => {
	return (
		<>
			<h2>What web projects have you worked on?</h2>
			<p>I have worked on various web projects in multiple different languages across the full stack. Here is a showcase of some highlights.</p>
			<div className="card-columns">
				{ projects }
			</div>
		</>
	);
};