import React from 'react';
import { useTrail, animated } from 'react-spring';
import { Link } from 'react-router-dom';
import newLinkData, { defaultTrail } from '../../utils/LinkData';
import './style.css';
import portrait from '../../assets/images/portrait.jpg';

export default () => {
	const links = newLinkData();
	links.push('/general/interests', 'Who are you?');
	links.push('/web/interests', 'Why are you interested in web development?');
	links.push('/web/projects', 'What web projects have you worked on?');
	links.push('/web/future', 'What are your future plans in web development?');
	links.push('/general/learn', 'Where can I learn more about you?');
	const trail = useTrail(links.data.length, defaultTrail);

	return (
		<>
			<img src={ portrait } alt="A portrait of Jonathan Chan" id="portrait" className="mb-4 ml-0 ml-md-3 rounded float-none float-md-right" />
			<h2>Good day, traveler!</h2>
			<p>I'd be happy to answer any questions you have for me…</p>
			<nav>
				<ul id="nav-menu">
					{
						trail.map((props, index) => (
							<animated.li key={ links.data[index].key } style={ props }>
								<Link to={ links.data[index].url }>{ links.data[index].text }</Link>
							</animated.li>
						))
					}
				</ul>
			</nav>
		</>
	);
};