import React from 'react';
import './style.css';

export default () => {
	return (
		<>
			<h2>Why are you interested in web development?</h2>
			<p>Websites are the reason I became interested in programming when I was younger. I started off teaching myself how to build websites with free tools my ISP provided for their members, and I quickly caught on to how HTML tags worked.</p>
			<p>CSS 2 had just started to take off, and I remember seeing a hyperlink change color and text decoration for the first time. Having an inherently creative mind, using CSS to control styles and layouts immediately sparked my wonder and interest.</p>
			<p>It was around this time that W3C standards compliance was also becoming popular. Those images you could add to your websites saying they were W3C standards compliant were like badges of honor. Learning how to build layouts without tables and transparent GIFs using semantic HTML gave me a huge sense of accomplishment.</p>
			<p>I then taught myself JavaScript by modifying and deploying various scripts from Dynamic Drive, JavaScript Kit, and irt.org.</p>
			<p>I also stumbled across the methodology of progressive enhancement which I still firmly believe in despite websites' ever-increasing dependence on JavaScript, which leads me to my current interest of studying WAI-ARIA.</p>
			<p>No matter what language, platform, or limitations a project requires, it's the same wonder, interest, and sense of accomplishment that carries me through my career as a programmer.</p>
		</>
	);
};